.Button {
  max-width: 480px;
  display: block;
  background-color: white;
  padding: 23px 24px 34px 24px;

  p {
    background-color: #fc5c42;
    border-radius: 8px;
    text-align: center;
    color: white;
    padding: 17px 7.63px;
    font-weight: bold;

    //17px
    font-size: 1.06rem;
    line-height: 22px;
    text-align: center;
    letter-spacing: -0.408px;
  }
}

.passive {
  p {
    background-color: #ccc;
  }
}
